import React from 'react';
import './App.css';
import _ from 'lodash';
const { GoogleSpreadsheet } = require('google-spreadsheet');
interface IProps {
}

interface IState {
  rows: IResultRow[];
  isLoading: boolean;
  standings: object;
  cutline: number;
}

interface IResultRow { 
  Time: string,
  User: string,
  G1: string,
  G1Score: number,
  G1Rank: number,
  G2: string,
  G2Score: number,
  G2Rank: number,
  G3: string,
  G3Score: number,
  G3Rank: number
}
class GolfApp extends React.Component<IProps,IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {
        isLoading: true,
        rows: [],
        standings: {},
        cutline: 100
    };
}
  
  async componentDidMount() {
    const doc = new GoogleSpreadsheet('1U3Sk_Vz4WMVfnVo0y1Ac03IbJebZRYbRBPQT_yRIDDE');
    doc.useApiKey('AIzaSyCOxznleeCup32GFr-iIzHBsaAmr73CLJs');
    await doc.loadInfo();
    const sheet = doc.sheetsByIndex[0];
    const rows = await sheet.getRows();

    let leaderboard = await fetch('https://www.masters.com/en_US/scores/feeds/scores.json');
    let data = await leaderboard.json();
    data = data.data;
    console.log(data);
    console.log(rows);
    
    let standings = data.standings;
    let newstandings = {};    
    let cutline = 10000; //data.cutLine;
    
    for(let i=0;i < data.player.length; i++) {
      //let isCut = data.player[i].position.displayValue === 'MC';
      let player : string = data.player[i].first_name + " " + data.player[i].last_name;
      //let displayRank = isCut ? data.players[i].roundScores[1].score.value : data.player[i].position.displayValue;
      //let rank = isCut ? data.player[i].roundScores[1].score.value : displayRank[0] === 'T' ? displayRank.substring(1)*1 : displayRank*1;
      //let score = isCut ? 'MC' : data.player[i].toPar.displayValue + ' (' + data.player[i].holesThrough.displayValue.replace('*','') + ')';
      
      let displayRank = "T1";
      let rank = 1;
      let score = "E";
      newstandings[player] = {rank, displayRank, score};
    }
    console.log(newstandings);

    for(let i=0; i<rows.length; i++) {
      console.log(rows[i]);
      console.log(rows[i].G1);
      console.log(rows[i].G2);
      console.log(newstandings[rows[i].G1]);
      console.log(newstandings[rows[i].G2]);
    }
    this.setState({
      isLoading: false,
      rows: rows,
      standings: newstandings,
      cutline: cutline
    });
    
  }
  render() {
    let results = this.state.rows;
    if(this.state.isLoading) {
      return (
        <div>Loading...</div>
      );
    }
    else {
      let standings = this.state.standings;
      let cutline = this.state.cutline;
      let cutlinedisp = cutline === 0 ? 'E' : cutline > 0 ? '+' + cutline : cutline+'';
      results = results.sort((b,a) => (standings[b.G1].rank+standings[b.G2].rank) - (standings[a.G1].rank+standings[a.G2].rank)  )
      return (
        <React.Fragment>
        <table>
          <tr><th>Name</th><th colSpan={3}>Pick 1</th><th colSpan={3}>Pick 2</th><th>Score</th></tr>
          {results.map(player => (
            <tr>
            <td>{ player.User }</td>
            <td style = {{
              backgroundColor:standings[player.G1].rank === 1 ? "Khaki" : standings[player.G1].score === 'MC' ? "lightCoral" : "none"
            }}>{ player.G1 }</td>
            <td style={{
                        textAlign:"center"
                      }}>{standings[player.G1].score}</td>
            <td style={{
                        textAlign:"center"
                      }}>{ standings[player.G1].displayRank}</td>
            <td style = {{
              backgroundColor:standings[player.G2].rank === 1 ? "Khaki" : standings[player.G2].score === 'MC' ? "lightCoral" : "none"
            }}>{ player.G2 }</td>
            <td style={{
                        textAlign:"center"
                      }}>{standings[player.G2].score }</td>
            <td style={{
                        textAlign:"center"
                      }}>{ standings[player.G2].displayRank}</td>
            <td style={{
                        textAlign:"center"
                      }}>{ standings[player.G1].rank+standings[player.G2].rank}</td>
            </tr>
          ))}
        </table>
        </React.Fragment>
      );
    } 
  }
}

export default GolfApp;
